*,
*::before,
*::after {
  box-sizing: border-box;
}

body {
  font-family: 'Open Sans', -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu,
    Cantarell, sans-serif;
  line-height: 1.1;
  color: #2a2a2a;
}

img {
  display: block;
  max-width: 100%;
  height: auto;
}

.search-form {
  display: flex;
  justify-content: center;
  padding: 8px;
  margin-bottom: 18px;
}
.btn {
  background-color: orange;
  color: #fff;
  cursor: pointer;
  border: none;
}

.btn:hover {
  background-color: rgb(38, 38, 153);
  cursor: pointer;
}
.button {
  display: block;
  margin-left: auto;
  margin-right: auto;
  margin-bottom: 10px;
  padding: 10px;
  background-color: orange;
  color: #fff;
  border: none;
  border-radius: 4px;
}
.button:hover {
  background-color: rgb(38, 38, 153);
  cursor: pointer;
}

.gallery {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(200px, 1fr));
  grid-gap: 20px;
  padding: 0;
  margin: 0;
  list-style: none;
  max-width: calc(100vw - 80px);
  margin-left: auto;
  margin-right: auto;
}

.gallery img {
  display: block;
  width: 100%;
  height: 200px;
  object-fit: cover;
  border-radius: 4px;
}
.photo-card img:hover {
  transform: scale(1.05);
}
.stats {
  display: flex;
}

.stats-item:not(:last-child) {
  margin-right: 10px;
}

.stats-item {
  text-align: center;
}
